import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ApiService} from '../../api.service';
import * as moment from 'moment';
import {AlertController, ToastController} from '@ionic/angular';
import {MatSnackBar} from '@angular/material';
@Component({
    selector: 'app-messages',
    templateUrl: './bookings.page.html',
    styleUrls: ['./bookings.page.scss']
})
export class BookingsPage implements OnInit {
    tab = 'first';
    tabName = 'New Bookings';
    starRating = 0;
    showSpinner = false;
    displayedColumns: string[] = ['serviceName', 'date & Time'];
    bookings: any = [];
    cancelledBookings: any = [];
    pastBookings: any = [];
    futureBookings: any = [];
    bookingsOnUi: any = [];
    handlerMessage = '';
    roleMessage = '';
    constructor(private toastController: ToastController, private snackBar: MatSnackBar, private adminService: ApiService, private alertController: AlertController) {
    }

    slideToggleEvent(event) {
        this.showCancelledBookings(event.checked);
    }

    onRatingsChanged(event, index) {
        if (!this.bookingsOnUi[index].isRatingAdded) {
            this.bookingsOnUi[index].showSpinner = true;
            this.bookingsOnUi[index].ratings = event;
            this.bookingsOnUi[index].isRatingAdded = true;
            setTimeout(() => {
                this.bookingsOnUi[index].showSpinner = false;
            }, 1000);
        } else {
            // this.presentToast('bottom', 'Appointment Cancelled', 1000, 'ionic-success-toast');
            this.presentToast('bottom', 'Ratings have already Added', 1000, 'ionic-error-toast').then();
        }

    }

    ngOnInit() {
        this.getBookings();
    }

    getBookings() {
        this.adminService.getBookings().subscribe(
            res => this.getBookingsSuccess(res),
            error => {
                this.adminService.commonError(error);
            }
        );
    }

    async presentToast(toastPosition, toastMessage, toastDuration, customClass) {
        const toast = await this.toastController.create({
            message: toastMessage,
            duration: toastDuration,
            position: toastPosition,
            cssClass: customClass
        });
        await toast.present();
    }

    async reasonAlert() {
        const alert = await this.alertController.create({
            header: 'Please enter the reason of cancellation',
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {
                        this.handlerMessage = 'Alert canceled';
                    },
                },
                {
                    text: 'Confirm',
                    role: 'confirm',
                    handler: (data) => {
                        // console.log(data);
                        // debugger;
                        if (data[0] && data[0].length > 0) {
                            this.presentToast('bottom', 'Appointment Cancelled', 1000, 'ionic-success-toast');
                            return true;
                        } else {
                            this.presentToast('bottom', 'Please submit reason before cancellation', 1000, 'ionic-error-toast');
                            return false;
                        }
                    },
                },
            ],
            inputs: [
                {
                    type: 'checkbox',
                    label: 'Reason 1',
                    value: 'value1',
                    checked: true
                },
                {
                    type: 'checkbox',
                    label: 'Reason 2',
                    value: 'value2'
                }
            ],
        });

        await alert.present();
    }

    async presentAlert() {
        const alert = await this.alertController.create({
            header: 'Do you want to cancel the Appointment!',
            cssClass: 'alert-popup',
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {
                        this.handlerMessage = 'Alert canceled';
                    },
                },
                {
                    text: 'OK',
                    role: 'confirm',
                    handler: () => {
                        this.reasonAlert();
                        this.handlerMessage = 'Alert confirmed';
                    },
                },
            ],
        });

        await alert.present();

        const { role } = await alert.onDidDismiss();
        this.roleMessage = `Dismissed with role: ${role}`;
    }

    getBookingsSuccess(res) {
        this.bookings = res;
        this.bookings = this.bookings.map((val) => {
            val.dateString = `${moment(val.date).format('DD MMM YYYY')}, ${val.timeSlot.startTime}`;
            val.showSpinner = false;
            val.isRatingAdded = false;
            return val;
        });
        this.futureBookings = this.bookings.filter(v => !v.isCancelled)
            .filter((val) => {
                if (moment(val.date) > moment()) {
                    return val;
                }
            });
        this.showCancelledBookings(false);
    }

    showCancelledBookings(event) {
        console.log(this.bookings, 'this.bookings');
        this.bookingsOnUi = !!(event) ? this.bookings.filter(v => v.isCancelled) : this.bookings.filter(v => !v.isCancelled).filter((val) => {
            if (moment(val.date) < moment()) {
                return val;
            }
        });
        console.log(this.bookingsOnUi);
    }

}
